<template lang="pug">
q-input.quan-input(rounded, standout, :class='inputClasses')
  template(#prepend, v-if='$slots.prepend')
    slot(name='prepend')
  template(#append, v-if='$slots.append')
    slot(name='append')
</template>

<script lang="ts">
import { computed, defineComponent } from 'vue';

export default defineComponent({
  props: {
    size: {
      type: String,
      default: 'md',
    },
  },

  setup(props) {
    const inputClasses = computed(() => {
      return {
        [`quan-input--size-${props.size}`]: props.size,
      };
    });

    return { inputClasses };
  },
});
</script>

